.main {

    &-bg {
        position: absolute;
        z-index: -1;
        top: 0;
        background: url(../images/bg/first-screen__bgc.png);
        max-height: 1000px;
        height: 100%;
        width: 100%;
        margin-bottom: 150px;

        @media(max-width:996.99px) {
            max-height: 700px;
        }
    }

    &-information {
        margin-bottom: 150px;

        @media(max-width:996.99px) {
            margin-bottom: 100px;
        }
        @media(max-width:556.99px) {
            margin-bottom: 60px;
        }
    }

    &-swiper {
        margin-bottom: 450px;

        @media(max-width:996.99px) {
            margin-bottom: 250px;
        }
    }

    &-mechanism {
        margin-bottom: 150px;

        @media(max-width:996.99px) {
            margin-bottom: 100px;
        }
        @media(max-width:556.99px) {
            margin-bottom: 60px;
        }
    }

    &-cards {
        margin-bottom: 150px;

        @media(max-width:996.99px) {
            margin-bottom: 100px;
        }
        @media(max-width:556.99px) {
            margin-bottom: 60px;
        }
    } 

    &-news {
        margin-bottom: 150px;

        @media(max-width:996.99px) {
            margin-bottom: 100px;
        }
    }
}

.swiper {

        &-wrapper {
        }

        &-slide {
            padding-top: 100px;
            display: flex;
            justify-content: space-between;
            align-items: flex-end;

            @media(max-width:996.99px) {
                padding-top: 0;
            }
        }

        &-wrap {

            &__text {
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 4;
                -webkit-box-orient: vertical;
                max-width: 600px;
                font-weight: 700;
                font-size: 40px;
                line-height: 140%;
                color: #FFFFFF;
                margin-bottom: 60px;
            }

            &__btn {
                cursor: pointer;
                display: inline-block;
                padding: 15px 30px;
                border: 1px solid #fff;
                font-weight: 600;
                font-size: 16px;
                line-height: 140%;
                color: #FFFFFF;
                transition: 0.3s ease;

                &:hover {
                    background: #fff;
                    color:#1840A7;
                    
                }
            }
        }

        &-group {

            &__undertext {
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                font-weight: 600;
                font-size: 16px;
                line-height: 140%;
                text-align: center;
                color: #FFFFFF;
                max-width: 250px;
                width: 100%;
            }
        }

        &-container {
            // display: flex;
            // align-items: center;
            // justify-content: end;
            // margin: 0 auto;
            position: relative;
            z-index: 2;
            margin-bottom: 30px;

            &__circle240 {
                position: absolute;
                z-index: 5;  
                top: 0;
                right: 0;
                transform: translate(0%, -100%);
            }

            &__number {
                position: absolute;
                z-index: 5;
                top: 0;
                right: 0;
                transform: translate(-30%, -250%);
                font-weight: 700;
                font-size: 40px;
                line-height: 140%;
            }
     }
}

.information-item {
    cursor: pointer;
    border: 2px solid rgba(24, 64, 167, 0.5);
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 40px 45px 45px;
    max-height: 450px;
    height: 100%;
    margin-bottom: 24px;
    transition: 0.5s ease;
    gap: 20px;

    @media(max-width:1100.99px) {
        padding: 20px 15px;
    }

    @media(max-width:996.99px) {
        flex-direction: column-reverse;
        justify-content: flex-end;
        align-items: center;
        text-align: center;
    }

    &:hover {
        background: #1840A7;

        .item-content__title, .item-content-list ul, .item-number {
            color: #FFFFFF;
        } 
    }
}

.item-content {

    &__title {
        font-weight: 700;
        font-size: 24px;
        line-height: 140%;  
        color: #333333;
        margin-bottom: 20px;
        transition: 0.5s ease;
    }

    &-list ul { 
        font-weight: 400;
        font-size: 16px;
        line-height: 140%;
        color: #333333;
        row-gap: 16px;
        transition: 0.5s ease;

        & li {
            margin-bottom: 16px;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}

.item-number {
    transition: 0.5s ease;
    font-weight: 700;
    font-size: 96px;
    line-height: 140%;
    color: #1840A7;
    opacity: 0.5;
}

.mechanism-item {

    &__title {
        font-weight: 700;
        font-size: 24px;
        line-height: 140%;
        color: #333333;
        margin-bottom: 40px;
    }

    &__text {
        font-weight: 400;
        font-size: 16px;
        line-height: 140%;
        color: #333333;
        margin-bottom: 40px;

        &:last-child {
            margin-bottom: 40px;
        }
    }

    &-buttons {
        text-align: center;

        @media(max-width:425px) {
            display: flex;
            flex-direction: column;
            gap: 20px;
        }

        &-btn {
            display: inline-block;
            padding: 10px 20px;
            border: 2px solid rgba(24,64,167,.5);
            margin: 0px 24px;
            font-weight: 600;
            font-size: 16px;
            line-height: 140%;
            color: #333333;
            transition: 0.5s ease;

            &:hover {
                background: #1840a7;
                color: #FFFFFF;
            }
        }
    }
}

.main-cards {
    text-align: center;

    &__title {
        font-weight: 700;
        font-size: 40px;
        line-height: 140%;
        text-align: center;
        margin-bottom: 80px;

        @media(max-width:996.99px) {
            font-size: 30px;
            margin-bottom: 40px;
        }

        @media(max-width:550.99px) {
            font-size: 24px;
            margin-bottom: 20px;
        }
    }

    &__text {
        max-width: 400px;
        border: 2px solid rgba(24, 64, 167, 0.5);
        padding: 20px 40px;
        display: inline-block;
        margin-bottom: 80px;

        @media(max-width:996.99px) {
            margin-bottom: 40px;
        }
    }

    &-block {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 24px;
        height: 100%;

        @media(max-width:650.99px) {
            flex-direction: column;
        }

        & .item {
            cursor: pointer;
            max-height: 100%;
            background: #1840A7;
            padding: 30px 60px;
            max-width: 450px;
            transition: 0.5s ease;

            @media(max-width:996.99px) {
                padding: 15px 20px;
            }

            &:hover {
              background: #091c4c;  
            }
    
            &__icon {
                max-width: 100px;
                height: 100px;
                margin-bottom: 30px;
            }
    
            &__text {
                
                font-weight: 700;
                font-size: 24px;
                line-height: 140%;
                color: #FFFFFF;
            }
        }
    }
}

.main-news {

    &__title {
        font-weight: 700;
        font-size: 40px;
        line-height: 140%;
        text-align: center;
        color: #333333;
        margin-bottom: 80px;

        @media(max-width:996.99px) {
            font-size: 30px;
            margin-bottom: 15px;
        }
    }

    &__btn {
        user-select: none;
        cursor: pointer;
        display: inline-block;
        font-weight: 600;
        font-size: 16px;
        line-height: 140%;
        color: #FFFFFF;
        padding: 15px 30px;
        background: #1840A7;

        @media(max-width: 425px) {
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
}

.news-item {
    cursor: pointer;
    border: 2px solid rgba(24, 64, 167, 0.5);
    padding: 24px;
    margin-bottom: 80px;
    transition: 0.5s ease;

    @media(max-width:768.99px) {
        margin-bottom: 20px;
    }

    @media(max-width:425.99px) {
        margin-bottom: 10px;
    }

    &:hover {
        background: #1840a7;

        .news-item__title, .news-item__text {
                color: #FFFFFF;
        }
    }

    &__picture img{
        width: 100%;
        margin-bottom: 20px;
    }

    &__title {
        font-weight: 600;
        font-size: 16px;
        line-height: 140%;
        color: #333333;
        margin-bottom: 20px;
        transition: 0.5s ease;
        
    }

    &__text {
        font-weight: 400;
        font-size: 14px;
        line-height: 140%;
        color: #333333;
        transition: 0.5s ease;
    }
}



.hidden {
    display: none;
}

.active {
    display: block;
}



.personal {
   
    &-content {
    }
}

.personal-content {

