                    font-size: 16px;
                    line-height: 140%;
                    color: #333333;
                }
            }
        }

        .production-title {
            font-weight: 700;
            font-size: 24px;
            line-height: 140%;
            text-align: center;
            color: #1840A7;
            margin-bottom: 40px;
        }

        .production-position {

