                    }

                    &__text {
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 140%;
                        color: #333333;
                    }
                }
            }

            &-groupbot {
                display: flex;
                align-items: center;
                justify-content: center;
                gap: 25px;

                .grouptop-item {
                    text-align: center;
                    flex: 0 0 280px;
                    

                    &__number {
                        display: inline-block;
                        border: 2px solid #1840A7;
                        padding: 12px 22px;
                        margin-bottom: 20px;
                    }

                    &__text {
                        font-weight: 400;
                        font-size: 16px;
