}

.navigation {
    margin-bottom: 60px;

    & ul {
        display: flex;
        align-items: center;    

        & li {
            display: flex;
            align-items: center;

            & a {
                font-weight: 400;
                font-size: 13px;
                line-height: 140%;
                color: #1840A7;
                border-bottom: 1px transparent;

                &:hover {
                    text-decoration: underline;
                }

            }

            & span {
                display: block;
                width: 5px;
                height: 5px; 
                background: #1840A7;
                margin: 0px 15px;
            }
        }
    }
    
}

.list {
    display: flex;
    flex-direction: column;
        
}

.list-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px 30px;
    background: #1840A7; 
    opacity: 0.3; 
    font-weight: 600;
    font-size: 16px;
    line-height: 140%;
    color: #FFFFFF;
    transition: 0.3s ease;

    &:hover {
        background: #1841a7;
        opacity: 1;
    }
}

.about {
    margin-bottom: 120px;

    &-content {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        gap: 80px;
    }
}

.content-number {

    &-item {
        border: 2px solid var(--accent, #1840A7);
        padding: 12px 22px;
        color: #1840A7;
        font-size: 24px;
        font-weight: 700;
        line-height: 140%;
    }

    & span {
        display: block;
        background: #1840A7;
        height: 150px;
        width: 2px;
        margin: 0 auto;
    }
}

.content-info {

    &-item {
        margin-bottom: 90px;
    }
}

.info-item__title {
    color: var(--black, #333);
    font-size: 24px;
    font-weight: 700;
    line-height: 140%;
    margin-bottom: 24px;
}

.info-item__subtitle {
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    color: var(--black, #333);
    font-size: 16px;
    line-height: 140%;
    // height: 90px;
}


.market {
    margin-bottom: 120px;

    &-content {

        &__title {
            color: var(--black, #333);
            font-size: 24px;       
            font-weight: 700;
            line-height: 140%;
            margin-bottom: 32px;
        }

        &__text {
            display: block;
            color: var(--black, #333);
            font-size: 16px;
            line-height: 140%;
            margin-bottom: 20px;
        }

        &-buttons {
            margin-top: 40px;

            &__btn {
                display: inline-block;
                background: var(--accent-50, rgba(24, 64, 167, 0.50));
                color: #FFF;
                font-size: 14px;
                font-weight: 600;
                line-height: 140%;
                padding: 10px 20px;
                margin-right: 20px;
                transition: 0.3s ease;
                &:hover {
                    background: var(--accent, #1840A7);
                    
                }
            }
        }
    }

}





